.button {
  width: 129px;
  height: 30px;
  /* background-color: #0066cc; */
  border-radius: 3px;
  padding: 5px, 24px, 5px, 24px;
  color: white;
  border: transparent;
  gap: 20px;
}

.disabled {
  background-color: #004d99;
  opacity: 50%;
  color: #002e5c;
}

.secondary {
  color: #0066cc;
  background-color: white;
  border: 1px solid #0066cc;
}

.default {
  background-color: #0066cc;
  color: #ffffff;
  width: 129px;
  height: 30px;
  border: 1px;
}

.red {
  background-color: red;
}

.default:hover {
  background-color: #0080ff;
}

.red:hover {
  background-color: #ff1a47;
}

.secondary:hover {
  background-color: #e5f0fa;
  border: 1px solid #0066cc;
  color: #0066cc;
}

.default:active {
  background-color: #0059b2;
}

.secondary:active {
  background-color: #c9e5ff;
}

.red:active {
  background-color: #b21232;
}

.button:disabled {
  background-color: #004d99;
  opacity: 50%;
  color: #002e5c;
}
